let listaDeProductos = [];

document.querySelector("#enlaceProductos").addEventListener("click", e => { 
    // e.preventDefault(); 
    document.querySelector("#agregar-producto").style.display = "none";
    document.querySelector("#listado-productos").style.display = "block";
    document.querySelector("#listado-productos h1").classList.add("my-4");
    document.querySelector(".table").classList.add("mt-4");
    document.querySelector("#enlaceAgregar").classList.remove("active");
    document.querySelector("#enlaceProductos").classList.add("active");
    document.querySelector("#navbarNav").classList.remove("show");
});

document.querySelector("#enlaceAgregar").addEventListener("click", e => { 
    // e.preventDefault(); 
    document.querySelector("#listado-productos").style.display = "none";
    document.querySelector("#agregar-producto").style.display = "block";
    document.querySelector("#enlaceAgregar").classList.add("active");
    document.querySelector("#enlaceProductos").classList.remove("active");
    document.querySelector("#navbarNav").classList.remove("show");
});

document.querySelector("#stock").addEventListener("click", e => { 
    // e.preventDefault(); 
    document.querySelector("#listado-productos").style.display = "none";
    document.querySelector("#agregar-producto").style.display = "block";
    document.querySelector("#enlaceAgregar").classList.add("active");
    document.querySelector("#enlaceProductos").classList.remove("active");
    document.querySelector("#navbarNav").classList.remove("show");
});

const form = document.querySelector("form");

const guardarDatos = e => {

    e.preventDefault();

    localStorage.getItem("Todos los productos") !== null && (listaDeProductos = JSON.parse(localStorage.getItem("Todos los productos")));

    const nombreDelProducto = document.querySelector("#nombre").value;
    const cantidad = document.querySelector("#cantidad").value;
    const precio = document.querySelector("#precio").value;
    const precioTotal = precio * cantidad;

    const producto = {
        nombreDelProducto,
        cantidad,
        precio,
        precioTotal,
    }

    listaDeProductos.push(producto);
    localStorage.setItem("Todos los productos", JSON.stringify(listaDeProductos));
    form.reset();
}

form.addEventListener("submit", guardarDatos);

const botonAgregar = document.querySelector(".agregar");

const mostrarDatos = e => {

    if(localStorage.getItem("Todos los productos") !== null) {
        // e.preventDefault();
        listaDeProductos = JSON.parse(localStorage.getItem("Todos los productos"));
        let tablaDeProductos = document.querySelector("#tablaDeProductos");
        tablaDeProductos.innerHTML = "";
        let contador = 0;
    
        listaDeProductos.forEach((element, index) => {
            tablaDeProductos.innerHTML += `<tr>
                <th scope="row"> ${++contador} </th>
                <td> ${element.nombreDelProducto} </td>
                <td> ${element.cantidad} ${element.cantidad > 1 ? "unidades" : "unidad"} </td>
                <td> $${element.precio} </td>
                <td> $${element.precioTotal} </td>
                <td> <button type="button" class="btn btn-danger eliminarItem" id="${index}" onclick="eliminarItem(event)"> - </button> </td>
            </tr>`; 
        });

        const precioFinal = listaDeProductos.reduce((acc, objeto) => acc + objeto.precioTotal, 0);

        if(listaDeProductos.length !== 0) {
            tablaDeProductos.innerHTML += `<tr class="fs-4 fw-semibold"> 
                <td colspan="4"> PRECIO FINAL: </td> 
                <td colspan="2"> $${precioFinal} </td>
            </tr>`;
        }
        
        // let borrarItem = document.querySelectorAll(".eliminarItem");

        // borrarItem.forEach((el, i) => el.addEventListener("click", eliminarPro = e => {
        //     // e.preventDefault();
        //     listaDeProductos.splice(i, 1);
        //     localStorage.setItem("Todos los productos", JSON.stringify(listaDeProductos));
        //     mostrarDatos();
        //     (listaDeProductos.length === 0) && localStorage.clear();
        // }));
    }
};

botonAgregar.addEventListener("click", mostrarDatos);

(localStorage.getItem("Todos los productos") !== null && location.hash !== ``) && mostrarDatos();

const eliminarItem = e => {
    let id = e.target.id;
    listaDeProductos.splice(id, 1);
    localStorage.setItem("Todos los productos", JSON.stringify(listaDeProductos));
    mostrarDatos();
    (listaDeProductos.length === 0) && localStorage.clear();
    // (localStorage.getItem("Todos los productos") === "[]") && localStorage.clear();
    // (localStorage.getItem("Todos los productos") === JSON.stringify([])) && localStorage.clear();
};

const borrarTodo = document.querySelector(".eliminarTodo");

const eliminarTodo = e => {
    // e.preventDefault();
    localStorage.clear();
    location.reload();
}

borrarTodo.addEventListener("click", eliminarTodo);